ex-end;\r\n    text-align: right;\r\n  }\r\n}\r\n\r\n.chat-block {\r\n  display: flex;\r\n  align-items: center;\r\n  padding: 15px;\r\n  background: $light;\r\n  border-radius: 6px;\r\n\r\n  img {\r\n    vertical-align: middle;\r\n    margin-right: 10px;\r\n  }\r\n}\r\n\r\n.chat-date {\r\n  font-size: 12px;\r\n  color: $muted;\r\n}\r\n\r\n/*******************\r\nChartistt chart css\r\n******************/\r\n.barchrt {\r\n  .ct-series-a .ct-bar {\r\n    stroke: $success;\r\n  }\r\n\r\n  .ct-series-b .ct-bar {\r\n    stroke: $info;\r\n  }\r\n}\r\n\r\n.linearea {\r\n  height: 280px;\r\n\r\n  .ct-series-a .ct-area {\r\n    fill-opacity: 0.05;\r\n    fill: $success;\r\n  }\r\n\r\n  .ct-series-a .ct-line,\r\n  .ct-series-a .ct-point {\r\n    stroke: $success;\r\n    stroke-width: 2px;\r\n  }\r\n\r\n  .ct-series-b .ct-area {\r\n    fill: $info;\r\n    fill-opacity: 0.1;\r\n  }\r\n\r\n  .ct-series-b .ct-line,\r\n  .ct-series-b .ct-point {\r\n    stroke: $info;\r\n    stroke-width: 2px;\r\n  }\r\n\r\n  .ct-series-c .ct-area {\r\n    fill: $danger;\r\n    fill-opacity: 0.1;\r\n  }\r\n\r\n  .ct-series-c .ct-line,\r\n  .ct-series-c .ct-point {\r\n    stroke: $danger;\r\n    stroke-width: 2px;\r\n  }\r\n\r\n  .ct-series-a .ct-point,\r\n  .ct-series-b .ct-point,\r\n  .ct-series-c .ct-point {\r\n    stroke-width: 6px;\r\n  }\r\n}\r\n\r\n.piechart {\r\n  .ct-series-a .ct-slice-donut {\r\n    stroke: $info;\r\n  }\r\n\r\n  .ct-series-b .ct-slice-donut {\r\n    stroke: $success;\r\n  }\r\n\r\n  .ct-series-c .ct-slice-donut {\r\n    stroke: $purple;\r\n  }\r\n\r\n  .ct-series-d .ct-slice-donut {\r\n    stroke: $light;\r\n  }\r\n\r\n  .ct-series-e .ct-slice-donut {\r\n    stroke: $warning;\r\n  }\r\n}\r\n\r\n/*******************\r\nPricing scss\r\n******************/\r\nhtml {\r\n  .popular-plan {\r\n    transform: scale(1.1);\r\n    z-index: 1;\r\n  }\r\n\r\n  .price-listing {\r\n    margin-bottom: 30px;\r\n\r\n    .mat-list-item {\r\n      height: auto;\r\n\r\n      .mat-list-item-content {\r\n        display: block;\r\n        padding: 20px 0;\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n/*******************\r\nChartis tooltip scss\r\n******************/\r\n.chartist-tooltip {\r\n  position: absolute;\r\n  display: inline-block;\r\n  opacity: 0;\r\n  min-width: 50px;\r\n  padding: 5px 10px;\r\n  border-radius: 5px;\r\n  background: #313131;\r\n  color: $white;\r\n  font-weight: 500;\r\n  text-align: center;\r\n  pointer-events: none;\r\n  z-index: 1;\r\n  -webkit-transition: opacity 0.2s linear;\r\n  -moz-transition: opacity 0.2s linear;\r\n  -o-transition: opacity 0.2s linear;\r\n  transition: opacity 0.2s linear;\r\n}\r\n\r\n/*.chartist-tooltip:before {\r\n    content: \"\";\r\n    position: absolute;\r\n    top: 100%;\r\n    left: 50%;\r\n    width: 0;\r\n    height: 0;\r\n    margin-left: -5px;\r\n    border: 5px solid transparent;\r\n    border-top-color: #313131; }\r\n*/\r\n.chartist-tooltip.tooltip-show {\r\n  opacity: 1;\r\n}\r\n\r\n.ct-area,\r\n.ct-line {\r\n  pointer-events: none;\r\n}\r\n\r\n/*# sourceMappingURL=chartist-plugin-tooltip.css.map */\r\n.no-wrap {\r\n  td,\r\n  .ng-star-inserted {\r\n    white-space: nowrap;\r\n  }\r\n}\r\n\r\n/*******************\r\nSmart table\r\n******************/\r\n.smart-table tr td,\r\n.smart-table tr th {\r\n  padding: 15px !important;\r\n  text-align: left;\r\n}\r\n\r\n.ng2-smart-action-add-add {\r\n  color: $white !important;\r\n  background: $themecolor;\r\n  padding: 8px 15px;\r\n  border-radius: $radius;\r\n  white-space: nowrap;\r\n}\r\n\r\n.ng2-smart-pagination-nav {\r\n  margin-left: auto;\r\n}\r\n\r\n.ng2-smart-pagination-nav .pagination > li > a {\r\n  line-height: 1rem;\r\n}\r\n\r\n.ng2-smart-filter .form-control {\r\n  border: 1px solid $border;\r\n  border-radius: 0.25rem;\r\n  min-height: 38px;\r\n}\r\n\r\n.form-control {\r\n  border: 1px solid $border;\r\n}\r\n\r\n/**********************\r\nOverall Responsive table\r\n***********************/\r\n\r\n.responsive-table {\r\n  display: flex;\r\n  flex-direction: column;\r\n  overflow: auto;\r\n\r\n  .mat-cell:first-of-type,\r\n  .mat-footer-cell:first-of-type,\r\n  .mat-header-cell:first-of-type {\r\n    padding-left: 0;\r\n  }\r\n\r\n  .header-label {\r\n    display: none;\r\n  }\r\n\r\n  .mat-table {\r\n    overflow: auto;\r\n    max-height: 100%;\r\n  }\r\n}\r\n\r\n.table-responsive {\r\n  overflow: auto;\r\n}\r\n\r\n.pagination {\r\n  display: -ms-flexbox;\r\n  display: flex;\r\n  padding-left: 0;\r\n  list-style: none;\r\n  border-radius: 0.25rem;\r\n}\r\n\r\n.page-link {\r\n  position: relative;\r\n  display: block;\r\n  padding: 0.5rem 0.75rem;\r\n  margin-left: -1px;\r\n  line-height: 1.25;\r\n  color: $themecolor;\r\n  background-color: $white;\r\n  border: 1px solid $border;\r\n}\r\n\r\n.page-link:hover {\r\n  z-index: 2;\r\n  color: $bodytext;\r\n  text-decoration: none;\r\n  background-color: $light;\r\n  border-color: $border;\r\n}\r\n\r\n.page-link:focus {\r\n  z-index: 2;\r\n  outline: 0;\r\n  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);\r\n}\r\n\r\n.page-link:not(:disabled):not(.disabled) {\r\n  cursor: pointer;\r\n}\r\n\r\n.page-item:first-child .page-link {\r\n  margin-left: 0;\r\n  border-top-left-radius: 0.25rem;\r\n  border-bottom-left-radius: 0.25rem;\r\n}\r\n\r\n.page-item:last-child .page-link {\r\n  border-top-right-radius: 0.25rem;\r\n  border-bottom-right-radius: 0.25rem;\r\n}\r\n\r\n.page-item.active .page-link {\r\n  z-index: 1;\r\n  color: $white;\r\n  background-color: $themecolor;\r\n  border-color: $themecolor;\r\n}\r\n\r\n.page-item.disabled .page-link {\r\n  color: $muted;\r\n  pointer-events: none;\r\n  cursor: auto;\r\n  background-color: $white;\r\n  border-color: $border;\r\n}\r\n\r\n.ticket-list {\r\n  td.mat-cell,\r\n  td.mat-footer-cell,\r\n  th.mat-header-cell {\r\n    padding: 0 15px !important;\r\n  }\r\n}\r\n\r\n.employee-list {\r\n  td.mat-cell,\r\n  td.mat-footer-cell,\r\n  th.mat-header-cell {\r\n    padding: 15px !important;\r\n  }\r\n\r\n  .d-flex {\r\n    align-items: center;\r\n  }\r\n}\r\n\r\n.chat-app {\r\n  .mat-list-item {\r\n    &.active {\r\n      background: rgba(0, 0, 0, 0.04);\r\n    }\r\n  }\r\n}\r\n\r\n.notes-app {\r\n  .mat-list-base .mat-list-item .mat-list-item-content,\r\n  .mat-list-base .mat-list-option .mat-list-item-content {\r\n    padding: 0 !important;\r\n  }\r\n\r\n  .mat-drawer-content {\r\n    height: calc(100vh - 190px) !important;\r\n  }\r\n\r\n  &.mat-card .mat-card-content {\r\n    padding: 0;\r\n    height: calc(100% - 64px);\r\n  }\r\n\r\n  .mat-input-element {\r\n    font-size: 14px !important;\r\n  }\r\n\r\n  .mat-form-field-appearance-legacy .mat-form-field-label {\r\n    top: 33px !important;\r\n    font-size: 14px !important;\r\n  }\r\n}\r\n\r\n.notes-color-panel {\r\n  min-width: 96px !important;\r\n\r\n  .mat-menu-content {\r\n    display: flex;\r\n\r\n    .mat-menu-item {\r\n      padding: 0 8px !important;\r\n    }\r\n\r\n    .mat-flat-button {\r\n      min-width: 20px !important;\r\n      padding: 0 !important;\r\n    }\r\n  }\r\n}\r\n\r\n.course-app {\r\n  .course-header {\r\n    .mat-card-header-text {\r\n      width: 100%;\r\n    }\r\n  }\r\n\r\n  .mat-card-content {\r\n    margin-bottom: 0 !important;\r\n  }\r\n}\r\n","/*\r\nTemplate Name: Material Pro Admin\r\nAuthor: Themedesigner\r\nEmail: niravjoshi87@gmail.com\r\nFile: scss\r\n*/\r\n\r\n$mini-sidebar-width: 80px;\r\n/*******************\r\nMain sidebar\r\n******************/\r\n\r\n.mat-sidenav {\r\n  width: 240px;\r\n  padding-top: 0px;\r\n}\r\n\r\n#snav {\r\n  box-shadow: 1px 0px 20px rgba(0, 0, 0, 0.08);\r\n\r\n  .mat-drawer-inner-container {\r\n    overflow: visible;\r\n    height: auto;\r\n  }\r\n\r\n  .mat-nav-list .mat-list-item {\r\n    height: auto;\r\n\r\n    &:hover {\r\n      background: none;\r\n    }\r\n\r\n    .mat-list-item-content {\r\n      display: block;\r\n      padding: 0px 0px;\r\n\r\n      &:hover {\r\n        background: none;\r\n\r\n        >a {\r\n          color: $themecolor;\r\n        }\r\n      }\r\n    }\r\n\r\n    a {\r\n      height: 45px;\r\n      padding: 0 10px;\r\n      margin-bottom: 10px;\r\n      align-items: center;\r\n      display: flex;\r\n      font-size: 15px;\r\n      white-space: nowrap;\r\n      color: $sidebar-text;\r\n\r\n      mat-icon:not(.dd-icon) {\r\n        margin-right: 8px;\r\n      }\r\n\r\n      mat-icon {\r\n        color: $sidebar-icons;\r\n      }\r\n\r\n      .dd-icon {\r\n        font-size: 16px;\r\n        width: 16px;\r\n        transition: 0.2s ease-in;\r\n        margin: 5px 0 5px 5px;\r\n        height: 16px;\r\n      }\r\n    }\r\n\r\n    &.selected>.mat-list-item-content>a {\r\n      background: $themecolor-alt;\r\n      border-radius: $radius;\r\n      color: $white;\r\n\r\n      mat-icon {\r\n        color: $white;\r\n      }\r\n\r\n      .dd-icon {\r\n        -webkit-transform: rotate(-180deg);\r\n        transform: rotate(-180deg);\r\n      }\r\n    }\r\n\r\n    &.selected .sub-item .mat-list-item-content a.selected {\r\n      background: transparent;\r\n      color: $inverse;\r\n      font-weight: 500;\r\n    }\r\n  }\r\n\r\n  .app-sidebar>.mat-nav-list>.mat-list-item>.mat-list-item-content {\r\n    padding: 0px 15px;\r\n  }\r\n\r\n  .mat-nav-list .sub-item {\r\n    display: none;\r\n    margin-top: -15px;\r\n\r\n    a {\r\n      padding-left: 40px;\r\n      height: 50px;\r\n      margin-bottom: 0px;\r\n    }\r\n\r\n    .child-sub-item {\r\n      display: none;\r\n      margin-top: -15px;\r\n\r\n      a {\r\n        padding: 0 0 0 55px;\r\n        height: 50px;\r\n        margin-bottom: 0px;\r\n      }\r\n    }\r\n  }\r\n\r\n  .saperator {\r\n    cursor: text;\r\n    font-size: 13px;\r\n    margin-bottom: 15px;\r\n    margin-top: 25px;\r\n    font-weight: 500;\r\n    white-space: nowrap;\r\n  }\r\n\r\n  .selected {\r\n    .sub-item {\r\n      display: block;\r\n\r\n      .selected+ {\r\n        .child-sub-item {\r\n          display: block;\r\n        }\r\n      }\r\n    }\r\n  }\r\n}\r\n\r\n/*******************\r\nuse profile section\r\n******************/\r\n\r\n.user-profile {\r\n  position: relative;\r\n  background-size: cover;\r\n\r\n  .profile-img {\r\n    width: 50px;\r\n    margin-left: 30px;\r\n    padding: 31px 0;\r\n    border-radius: 100%;\r\n\r\n    &::before {\r\n      -webkit-animation: 2.5s blow 0s linear infinite;\r\n      animation: 2.5s blow 0s linear infinite;\r\n      position: absolute;\r\n      content: '';\r\n      width: 50px;\r\n      height: 50px;\r\n      top: 31px;\r\n      margin: 0 auto;\r\n      border-radius: 50%;\r\n      z-index: 0;\r\n    }\r\n\r\n    @-webkit-keyframes blow {\r\n      0% {\r\n        box-shadow: 0 0 0 0px rgba(0, 0, 0, 0.1);\r\n        opacity: 1;\r\n        -webkit-transform: scale3d(1, 1, 0.5);\r\n        transform: scale3d(1, 1, 0.5);\r\n      }\r\n\r\n      50% {\r\n        box-shadow: 0 0 0 10px rgba(0, 0, 0, 0.1);\r\n        opacity: 1;\r\n        -webkit-transform: scale3d(1, 1, 0.5);\r\n        transform: scale3d(1, 1, 0.5);\r\n      }\r\n\r\n      100% {\r\n        box-shadow: 0 0 0 20px rgba(0, 0, 0, 0.1);\r\n        opacity: 0;\r\n        -webkit-transform: scale3d(1, 1, 0.5);\r\n        transform: scale3d(1, 1, 0.5);\r\n      }\r\n    }\r\n\r\n    @keyframes blow {\r\n      0% {\r\n        box-shadow: 0 0 0 0px rgba(0, 0, 0, 0.1);\r\n        opacity: 1;\r\n        -webkit-transform: scale3d(1, 1, 0.5);\r\n        transform: scale3d(1, 1, 0.5);\r\n      }\r\n\r\n      50% {\r\n        box-shadow: 0 0 0 10px rgba(0, 0, 0, 0.1);\r\n        opacity: 1;\r\n        -webkit-transform: scale3d(1, 1, 0.5);\r\n        transform: scale3d(1, 1, 0.5);\r\n      }\r\n\r\n      100% {\r\n        box-shadow: 0 0 0 20px rgba(0, 0, 0, 0.1);\r\n        opacity: 0;\r\n        -webkit-transform: scale3d(1, 1, 0.5);\r\n        transform: scale3d(1, 1, 0.5);\r\n      }\r\n    }\r\n\r\n    img {\r\n      width: 100%;\r\n      border-radius: 100%;\r\n    }\r\n  }\r\n\r\n  .profile-text {\r\n    padding: 5px